.modal-responsive {
  max-height: 500px;
  min-height: 500px !important;
}

.modal-block {
  display: none;
  position: fixed;
  z-index: 1;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: black;
}

